.price {
  padding-top: 240px;

  @media (max-width: $tablet-width-max) {
    padding-top: 112px;
  }
}

.price__container {
  display: flex;

  @media (max-width: $tablet-width-max) {
    flex-direction: column;
  }
}

.price__wrapper {
  max-width: 370px;
  padding-top: 115px;
  padding-left: 80px;
  order: 1;

  @media (max-width: $tablet-width-max) {
    max-width: none;
    margin-bottom: 30px;
    padding-top: 0;
    padding-left: 0;
    text-align: center;
    order: 0;
  }

  @media (max-width: $mobile-width-max) {
    margin-bottom: 40px;
  }
}

.price__title {
  margin-bottom: 30px;

  &::after {
    width: 175px;
    margin-top: 20px;
  }

  @media (max-width: $tablet-width-max) {
    margin-bottom: 20px;
  }
}

.price__text {
  margin: 0;
  margin-bottom: 60px;
  font-size: 16px;
  line-height: 27px;
  letter-spacing: 0;

  @media (max-width: $tablet-width-max) {
    margin-bottom: 33px;
  }

  @media (max-width: $mobile-width-max) {
    margin-bottom: 18px;
  }
}

.price__call {
  display: flex;
  flex-direction: column;
  text-align: right;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: normal;

  @media (max-width: $tablet-width-max) {
    text-align: center;
  }
}

.price__call-text {
  margin-right: 30px;
  margin-bottom: 14px;
  font-weight: 500;
  font-size: 22px;
  line-height: 34px;

  @media (max-width: $tablet-width-max) {
    margin-right: 0;
    margin-bottom: 10px;
    font-size: 18px;
    line-height: 34px;
  }
}

.price__call-link {
  position: relative;
  margin-right: 35px;
  color: inherit;

  &:hover,
  &:active {
    transition: color 0.2s ease-out;
    color: var(--color-accent);
  }

  @media (max-width: $tablet-width-max) {
    margin-right: 0;
    font-size: 18px;
    line-height: 32px;
    text-decoration-line: underline;
  }
}

.price__call-img {
  display: block;
  position: absolute;
  width: 30px;
  height: 30px;
  top: -1px;
  left: -3px;

  @media (max-width: $tablet-width-max) {
    display: none;
  }
}

.price__call-img-path {
  stroke: var(--color-accent);
}

.price__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media (max-width: $mobile-width-max) {
    justify-content: center;
  }
}

.price__item {
  display: flex;
  width: 50%;
  max-width: 370px;
  min-height: 672px;
  margin-right: 30px;

  // &:nth-child(odd) {
  //   margin-right: auto;
  // }

  @media (max-width: $tablet-width-max) {
    max-width: 288px;
    min-height: 629px;
    margin: 0 25px 30px;
  }

  @media (max-width: $mobile-width-max) {
    width: 100%;
    min-height: 614px;
    margin: 0 auto;
  }
}
